<template>
	<section>

		<div class="formContent" style="margin: 20px 0;" >
			<el-button type="primary" size="medium" @click="addRedPack" icon="el-icon-search" ></el-button>
		
		</div>
		<div class="tableContent">
		  <el-table :data="tableData" border style="width: 90%">		    
		    <el-table-column  prop="id" label="ID" width="80" >	
			</el-table-column>
		    <el-table-column  prop="name" label="">
			</el-table-column>		
		    <el-table-column   prop="sendName" label="">	    	
			</el-table-column>
		    <el-table-column   prop="totalAmount" label="">	    	
			</el-table-column>
		    <el-table-column   prop="totalNum" label="">	    	
			</el-table-column>
		    <el-table-column   prop="receivedNum" label="">	    	
			</el-table-column>
		    <el-table-column  label="" width="180" >
		      <template slot-scope="scope">
			       <p>{{scopeTxt[scope.row.sendScope]}}</p>
		      </template>			    	
			</el-table-column>

		    <el-table-column  prop="statusRemark" label="" >			    	
			</el-table-column>

		    <el-table-column  label="">
		      <template slot-scope="scope">
		        <el-button @click="toEdit(scope.row)" type="text" size="small"></el-button>
		        <el-button v-if="scope.row.status==1" @click="handleRefund(scope.row.id)" type="text" size="small"></el-button>
		      </template>		    		
		    </el-table-column>
		  </el-table>			
		</div>
	   <div class="pagi">
		   <el-pagination
		      @size-change="handleSizeChange"
		      @current-change="handleCurrentChange"
		      :current-page="currentPage4"
		      :page-sizes="[10, 20, 30, 40]"
		      :page-size="10"
		      layout="total, sizes, prev, pager, next, jumper"
		      :total="total">
		    </el-pagination>	   	
	   </div>
		<el-dialog
		  :title="diaTitle"
		  :visible.sync="dialogVisible"
		  width="30%">
				<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="140px" class="demo-ruleForm">
				  
				  <el-form-item label="" prop="sendName">
				    <el-input v-model="ruleForm.sendName"></el-input>
				  </el-form-item>				  
				  <el-form-item label="" prop="name">
				    <el-input v-model="ruleForm.name"></el-input>
				  </el-form-item>
				  <el-form-item label="" prop="wishing">
				    <el-input v-model="ruleForm.wishing"></el-input>
				  </el-form-item>				  
				  <el-form-item label="" prop="totalAmount">
				    <el-input type="number" v-model="ruleForm.totalAmount"></el-input>
				  </el-form-item>					  
				  <el-form-item label="" prop="totalNum">
				    <el-input type="number" v-model="ruleForm.totalNum"></el-input>
				  </el-form-item>	
				  <el-form-item label="" prop="remark">
				    <el-input  v-model="ruleForm.remark"></el-input>
				  </el-form-item>
				  <el-form-item label="" prop="subjectType">
				    <el-radio-group v-model="ruleForm.subjectType">
				      <el-radio label="0"></el-radio>
				      <el-radio label="1"></el-radio>
				    </el-radio-group>	
				  </el-form-item>				  
				  <el-form-item label="" prop="sendScope" >
				    <el-radio-group v-model="ruleForm.sendScope">
				      <el-radio label="0"></el-radio>
				      <el-radio label="1"></el-radio>
				      <el-radio label="2"></el-radio>
				    </el-radio-group>				  		
				  </el-form-item>
				  <el-form-item label="ID" prop="subjectId" v-show="ruleForm.sendScope==0"  >
				    <el-input type="number" v-model="ruleForm.subjectId"></el-input>
				  </el-form-item>					  
				  <el-form-item label="" prop="areaCode" v-show="ruleForm.sendScope==1">
				    <el-input type="number" v-model="ruleForm.areaCode"></el-input>
				  </el-form-item>					  
				  <el-form-item label=""  required>
				     <el-checkbox label="" v-model="ruleForm.ruleShare" name="type"></el-checkbox>
 					 <el-checkbox label="" v-model="ruleForm.ruleTrade" name="type"></el-checkbox>
				  </el-form-item>
				</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="dialogVisible = false"> </el-button>
		   <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
		  </span>
		</el-dialog>

	</section>
</template>

<script>

import {RedPacketList,RedPacketRefund,RedPacketSave} from '@/common/service/activityMana.js'
	
  export default {
    data() {
      var validate1 = (rule, value, callback) => {
        	 
        if(this.ruleForm.sendScope==0){
        	if(value === ''){
        		callback(new Error('ID'));
        	}else{
        		 callback();
        	}
        }else{
        	callback();
        }
      };    	
      var validate2 = (rule, value, callback) => {

        if(this.ruleForm.sendScope==1){
        	if(value === ''){
        		callback(new Error(''));
        	}else{
        		 callback();
        	}
        }else{
        	callback();
        }
      };      	
      return {
      	diaTitle:'',
      	dialogVisible:false,
      	regdate:'',
      	total:0,
      	statusTxt:['','','',''],
      	scopeTxt:['','',''],
      	searchForm:{
      		page:0,
      		size:10,
      		sort:'',
      		status:'',
      	},
      	currentPage4:1,
        tableData: [],      	
        ruleForm: {
			userId:'',//integer(query)	id
			sendName:'', //*string(query)	
			name:'',// *string(query)	
			wishing:'',// *string(query)	
			totalAmount:'',// *number(query)	
			totalNum:'',// *integer(query)	
			remark:'',// string(query)	
			sendScope:'2',// *string(query)	 0 1 2	
			subjectType:'',// *integer(query)	 0 1
			subjectId:'',//integer(query)	sendScope0ID	
			areaCode:'',//string(query)	sendScope1
			ruleShare:'',// *	boolean(query)	-
			ruleTrade:'',// *boolean(query)		-        	
        },
        rules: {
          name: [
            { required: true, message: '', trigger: 'blur' },
          ],
          sendName: [
            { required: true, message: '', trigger: 'blur' },
          ],          
          wishing: [
            { required: true, message: '', trigger: 'blur' },
          ],           
          totalAmount: [
            { required: true, message: '', trigger: 'blur' },
          ],  
          totalNum: [
            { required: true, message: '', trigger: 'blur' },
          ],           
          remark: [
            { required: true, message: '', trigger: 'blur' },
          ],              
          sendScope: [
            { required: true, message: '', trigger: 'blur' },
          ],             
          subjectType: [
            { required: true, message: '', trigger: 'blur' },
          ], 
          subjectId: [
            { validator: validate1, trigger: 'blur' },
          ],           
          areaCode: [
            { validator: validate2, trigger: 'blur'  },
            
          ],             

        }
      }
    },
    /**/
    watch: {

    },
    methods: {
	  addRedPack(){
	  	this.dialogVisible = true;
	  	this.diaTitle = '';
	  	this.ruleForm.id = '';
        this.ruleForm = {
			userId:'',//integer(query)	id
			sendName:'', //*string(query)	
			name:'',// *string(query)	
			wishing:'',// *string(query)	
			totalAmount:'',// *number(query)	
			totalNum:'',// *integer(query)	
			remark:'',// string(query)	
			sendScope:'2',// *string(query)	 0 1 2	
			subjectType:'',// *integer(query)	 0 1
			subjectId:'',//integer(query)	sendScope0ID	
			areaCode:'',//string(query)	sendScope1
			ruleShare:'',// *	boolean(query)	-
			ruleTrade:'',// *boolean(query)		-        	
        };	  	
	  },
	  toEdit(row){
	  	this.ruleForm = Object.assign({},row);
	  	this.ruleForm.sendScope = String(this.ruleForm.sendScope);
	  	this.ruleForm.subjectType = String(this.ruleForm.subjectType);
	  	this.ruleForm.totalAmount  =  String(this.ruleForm.totalAmount);
	  	this.ruleForm.totalNum  =  String(this.ruleForm.totalNum);
	  	this.dialogVisible = true;
	  	this.diaTitle = '';	  	
	  },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            
            this._RedPacketSave(this.ruleForm)
            
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },

      handleRefund(id){
      	
	        this.$confirm(', ?', '', {
	          confirmButtonText: '',
	          cancelButtonText: '',
	          type: 'warning'
	        }).then(() => {
				RedPacketRefund({id:id}).then((res)=>{
		     		this.$message({
		     			type:'success',
		     			message:''
		     		})
		     		this._RedPacketList(this.searchForm);
				}).catch((res)=>{
					
		     		this.$message({
		     			type:'error',
		     			message:res.message
		     		})					
					
				})
				
			
	        }).catch(() => {
         
	        });      	
      	
      },
      _RedPacketSave(opt){
      	
      	RedPacketSave(opt).then((res)=>{

		     		if(opt.id){
			     		this.$message({
			     			type:'success',
			     			message:''
			     		});		     			
		     		}else{
			     		this.$message({
			     			type:'success',
			     			message:''
			     		});			     			
		     		}
		     		
		     		
      		this.dialogVisible = false;
      	    this._RedPacketList(this.searchForm);	
      	}).catch((res)=>{
      		this.$message({
     			type:'error',
     			message:res.message
     		})     		
      	})
      	
      },
     _RedPacketList(opt){
     	RedPacketList(opt).then((res)=>{
				this.tableData = res.content;
				this.total = res.totalElements; 
     	}).catch((res)=>{
     		this.$message({
     			type:'error',
     			message:res.message
     		})
     	})
     },
     search(){
		this.searchForm.page = 0;
		this._RedPacketList(this.searchForm);     	
     },

      handleSizeChange(val) {
        console.log(` ${val} `);
        this.searchForm.size = val
		this.searchForm.page = 0;
		this._RedPacketList(this.searchForm);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
		this.searchForm.page = val-1;
		this._RedPacketList(this.searchForm);        
      }	
    },
    mounted(){
    	this._RedPacketList(this.searchForm);
    }
  }
</script>

<style lang="scss" scoped="scoped">
	.tap{
		height: 60px;
		line-height: 60px;
		width: 100%;
	}
	.el-table__header{
		text-align: center;
	}
	.pagi{
		width: 90%;
		text-align: center;
		margin-top: 30px;
	}
</style>


