<template>
	<section>

		<div class="formContent">

			<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="demo-ruleForm">
				<el-form-item label="" prop="title">
					<el-input v-model="ruleForm.title"></el-input>
				</el-form-item>
				<el-form-item label="ID" prop="toUserId">
					<el-input v-model="ruleForm.toUserId" style="width: 200px;"></el-input>
					<span class="psmsg">ps:</span>
				</el-form-item>
				<el-form-item label="" prop="name">
					<el-upload v-loading="uploading" class="avatar-uploader" action="" :show-file-list="false" :before-upload="beforeAvatarUpload">
						<img v-if="ruleForm.image" :src="ruleForm.image" class="avatar">
						<i v-else class="el-icon-plus avatar-uploader-icon"></i>
					</el-upload>
				</el-form-item>
				<el-form-item label="" prop="areaCodes">
					<p class="psmsg"></p>
					<el-tree :data="areadata" v-loading="a_loading" show-checkbox node-key="id" ref="tree" highlight-current :props="defaultProps">
					</el-tree>
				</el-form-item>
				<el-form-item label="" prop="type">
					<el-radio-group v-model="ruleForm.type">
						<el-radio label="0"></el-radio>
						<el-radio label="1"></el-radio>
					</el-radio-group>
				</el-form-item>

				<el-form-item label="" prop="linkType">
					<el-radio-group v-model="ruleForm.linkType">
						<el-radio label="1">URL</el-radio>
						<el-radio label="2"></el-radio>
						<el-radio label="3"></el-radio>
						<el-radio label="4"></el-radio>
						<el-radio label="5"></el-radio>
						<el-radio label="6"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="ID" prop="linkId" v-if="ruleForm.linkType!=1">
					<el-input v-model="ruleForm.linkId" style="width: 200px;"></el-input>
				</el-form-item>
				<el-form-item label="URL" prop="url" v-if="ruleForm.linkType==1">
					<el-input v-model="ruleForm.url"></el-input>
				</el-form-item>
				<el-form-item label="" prop="content">
					<el-input type="textarea" :rows="4" v-model="ruleForm.content"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="submitForm('ruleForm')" v-loading="subloading"></el-button>
				</el-form-item>
			</el-form>

		</div>
	</section>
</template>

<script>
	import { sendMessage } from '@/common/service/message.js'
	import { getArea, upload } from '@/common/service/activityMana.js'
	export default {
		data() {
			return {
				uploading: false,
				userInfo:{},
				ruleForm: {
					fromUserId: '', //integer(query)	ID			
					toUserId: '', // integer(query)	ID,
					areaCodes: '', //	string(query)	020
					type: '0', //integer(query)	01
					linkType: '1', // *integer(query)	:1URL;2;3;4;5;6	
					linkId: '', // integer(query)	Id
					image: '', //string(query)	
					title: '', // *string	(query)		
					url: '', // string(query)	URL linkType1	
					content: '', // *string(query)
				},
				rules: {
					title: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ],
					type: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ],
					linkType: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ],
					content: [{
						required: true,
						message: '',
						trigger: 'blur'
					}, ],

				},
				a_loading: true,
				subloading: false,
				areadata: [],
				alldata: [],
				defaultProps: {
					children: 'children',
					label: 'name'
				}

			}
		},

		methods: {
			getCheckedNodes() {
				console.log(this.$refs.tree.getCheckedNodes());
			},
			beforeAvatarUpload(file) {
				const isJPG = file.type === 'image/jpeg';
				const isPNG = file.type === 'image/png';
				const isLt1M = file.size / 1024 / 1024 < 1;

				if(!isJPG && !isPNG) {
					this.$message.error(' JPGPNG !');
					return false;
				}
				if(!isLt1M) {
					this.$message.error(' 1MB!');
					return false;
				}

				let fd = new FormData();

				fd.append('file', file);
				this.uploading = true;
				upload(fd).then((res) => {

					this.ruleForm.image = 'http://outmao.com:8088/yr/' + res[0];
					this.uploading = false;
				}).catch((res) => {
					this.uploading = false;
					this.$message.error(res.message);
				})

				return false
			},
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if(valid) {
						let arealist = this.$refs.tree.getCheckedNodes();
						let codeList = [];

						arealist.forEach((item) => {

							codeList.push(item.areacode);

						})
						this.ruleForm.fromUserId = this.userInfo.id;
						this.ruleForm.areaCodes = codeList.join(',');
						this.subloading = true;
						sendMessage(this.ruleForm).then((res) => {
							this.subloading = false;
							this.$message({
								type:'success',
								message:''
							});
							
							this.$router.push({path:'/message'})
							
							
						}).catch((res) => {
							this.subloading = false;
							this.$message.error(res.message);
						})

					} else {
						console.log('error submit!!');
						return false;
					}
				});
			},
			getToken(){
			  return JSON.parse(sessionStorage.getItem('user'));
			},
			_getArea() {
				let vm = this;
				this.a_loading = true;
				getArea().then((res) => {
					this.alldata = res;
					let allprovince = [],
						allCitys = [],
						allDistricts = [];
					res.forEach((item) => {
						if(item.level == 1) {
							allprovince.push(item);
						} else if(item.level == 2) {
							allCitys.push(item);
						} else {
							allDistricts.push(item);
						}
					});
					allCitys.forEach((item) => {
						item.children = [];
						allDistricts.forEach((item1) => {
							if(item.id == item1.parentId) {
								item.children.push(item1);
							}
						})

					})

					allprovince.forEach((item) => {
						item.children = [];
						allCitys.forEach((item1) => {
							if(item.id == item1.parentId) {
								item.children.push(item1);
							}
						})

					})

					this.areadata = allprovince;

					this.a_loading = false;

				}).catch((res) => {
					this.$message({
						type: 'error',
						message: res.message
					})
				})

			},
		},
		mounted() {
			this._getArea();
			this.userInfo = this.getToken();
		}
	}
</script>

<style lang="scss" scoped="scoped">
	.formContent {
		margin-top: 30px;
	}
	
	.psmsg {
		margin: 0;
		margin-left: 20px;
		color: #999;
	}
	
	.demo-ruleForm {
		width: 70%;
	}
	
	.tap {
		height: 60px;
		line-height: 60px;
		width: 100%;
	}
	
	.el-table__header {
		text-align: center;
	}
	
	.pagi {
		width: 90%;
		text-align: center;
		margin-top: 30px;
	}
</style>


